import { createServerClient } from "@supabase/ssr"
import { NextResponse, type NextRequest } from "next/server"

//  API 
const PROTECTED_API_ROUTES = [
  "/api/dify-chat",
  "/api/dify-upload",
  "/api/chat-session",
  "/api/save-message",
  "/api/save-essay-review",
  "/api/user/update",
]

//  API 
const PUBLIC_API_ROUTES = [
  "/api/auth/",           // 
  "/api/payment/xunhupay/notify", // 
  "/api/providers",       // 
]

export async function updateSession(request: NextRequest) {
  const url = request.nextUrl
  const pathname = url.pathname
  const code = url.searchParams.get("code")

  //  OAuth 
  if (code && !pathname.startsWith("/auth/callback")) {
    const callbackUrl = url.clone()
    callbackUrl.pathname = "/auth/callback"
    return NextResponse.redirect(callbackUrl)
  }

  let supabaseResponse = NextResponse.next({
    request,
  })

  const supabase = createServerClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!,
    {
      cookies: {
        getAll() {
          return request.cookies.getAll()
        },
        setAll(cookiesToSet) {
          cookiesToSet.forEach(({ name, value }) => request.cookies.set(name, value))
          supabaseResponse = NextResponse.next({
            request,
          })
          cookiesToSet.forEach(({ name, value, options }) => supabaseResponse.cookies.set(name, value, options))
        },
      },
    },
  )

  // ========================================
  //  API 
  // ========================================
  
  // 
  const isPublicRoute = PUBLIC_API_ROUTES.some(route => pathname.startsWith(route))
  if (isPublicRoute) {
    return supabaseResponse
  }

  //  API 
  const isProtectedApiRoute = PROTECTED_API_ROUTES.some(route => pathname.startsWith(route))
  
  if (isProtectedApiRoute) {
    // 1:  Supabase Session
    const { data: { user } } = await supabase.auth.getUser()
    
    // 2:  Authorization Header ( userId)
    const authHeader = request.headers.get("Authorization")
    const userId = request.headers.get("X-User-Id")
    
    //  Supabase  userId header
    if (!user && !userId) {
      console.warn(` [Middleware] : ${pathname}`)
      return NextResponse.json(
        { error: "", code: "UNAUTHORIZED" },
        { status: 401 }
      )
    }
    
    // 
    console.log(` [Middleware] : ${pathname} | User: ${user?.id || userId}`)
  }

  return supabaseResponse
}
